const inputElement = document.getElementsByClassName('price-control__input--to')[0];

const newMaxPrice = () => {
  if (window.innerWidth < 768) {
    inputElement.placeholder = '123';
    inputElement.value = '123';
  } else {
    inputElement.placeholder = '900';
    inputElement.value = '900';
  }
};

window.addEventListener('resize', newMaxPrice);



export {newMaxPrice};
